/* global console, document, Excel, Office */

// import { factorialRow } from "../functions/functions";
import RequestContext = Excel.RequestContext;

export const VERSION = "1.0.0.12";

// The initialize function must be run each time a new page is loaded
Office.onReady(() => {
  const lSideLoadMsg: HTMLElement | null = document?.getElementById("sideload-msg");
  const lAppBody: HTMLElement | null = document?.getElementById("app-body");
  const lRunButton: HTMLElement | null = document?.getElementById("runButton");

  if (lSideLoadMsg) {
    lSideLoadMsg.style.display = "none";
  }
  if (lAppBody) {
    lAppBody.style.display = "flex";
  }

  if (lRunButton) {
    lRunButton.onclick = onButtonClick;
  }
  console.log("Office.onReady");
  console.log("Manifest Version:", VERSION);
  // CustomFunctions.associate("FACTORIALROW", factorialRow);
});

export async function onButtonClick() {
  try {
    await Excel.run(async (context) => {
      /**
       * Insert your Excel code here
       */
      const range = context.workbook.getSelectedRange();

      // Read the range address
      range.load("address");

      // Update the fill color
      range.format.fill.color = "yellow";

      await context.sync();
      console.log(`The range address was ${range.address}.`);
    });
  } catch (error) {
    console.error(error);
  }
}

async function loadSetting(): Promise<string> {
  let mode: string | null = "column"; // Значение по умолчанию

  if (typeof OfficeRuntime !== "undefined" && OfficeRuntime.storage) {
    mode = await OfficeRuntime.storage.getItem("orientationMode");
  }

  if (!mode) {
    mode = "column"; // Значение по умолчанию
    if (typeof OfficeRuntime !== "undefined" && OfficeRuntime.storage) {
      await OfficeRuntime.storage.setItem("orientationMode", mode); // Сохранить его сразу
    }
  }

  console.log("Загруженное значение:", mode);
  return mode;
}

document.addEventListener("DOMContentLoaded", async (): Promise<void> => {
  console.log("DOMContentLoaded");
  const rowOption = document.getElementById("rowOption") as HTMLInputElement;
  const columnOption = document.getElementById("columnOption") as HTMLInputElement;

  // Загрузка сохранённого значения
  // const saved = OfficeRuntime.storage.getItem("orientationMode");
  const saved = await loadSetting();

  console.log("TASKPANE Режим из storage:", saved);
  if (saved === "column") {
    columnOption.checked = true;
  } else {
    rowOption.checked = true;
  }

  // Обработчики событий
  rowOption.addEventListener("change", async (): Promise<void> => {
    try {
      await onRadioButtonChange(rowOption);
    } catch (e) {
      console.error("Ошибка при переключении радио:", e);
    }

  });

  columnOption.addEventListener("change", async (): Promise<void> => {
    try {
      await onRadioButtonChange(columnOption);
    } catch (e) {
      console.error("Ошибка при переключении радио:", e);
    }
  });
});

export async function onRadioButtonChange(aHTMLInputElement: HTMLInputElement): Promise<void> {
  if (aHTMLInputElement.checked) {
    await saveSetting(aHTMLInputElement.value as "row" | "column");
  }
  console.log("onRadioButtonChange before Excel.run:");
  // пример пересчета Excel
  await Excel.run(async (context: RequestContext): Promise<void> => {
    console.log("onRadioButtonChange IN Excel.run:");
    context.workbook.application.calculate(Excel.CalculationType.full);

    let sheet = context.workbook.worksheets.getActiveWorksheet();
    let cell1 = sheet.getRange("B6");
    cell1.values = [["=1+1"]];
    console.log("Результат функции:", cell1.values);

    await context.sync();
  }).catch(error => console.log(error));
}

async function saveSetting(mode: "row" | "column"): Promise<void> {
  if (typeof OfficeRuntime !== "undefined" && OfficeRuntime.storage) {
    await OfficeRuntime.storage.setItem("orientationMode", mode); // Сохранить его сразу
  }
  console.log("Настройка сохранена:", mode);
}
